import { Component, OnInit, Input } from '@angular/core';
import { Project } from '../../model';
import { DatasourceService } from '../../Services/datasource.service';

@Component({
  selector: 'app-events',
  templateUrl: './events.component.html',
  styleUrls: ['./events.component.css']
})
export class EventsComponent implements OnInit {

  events: Project[];
  @Input() public countLimit: number;
  @Input() public showMorebtn: boolean;
  constructor(private datasource: DatasourceService) { }

  ngOnInit() {
    let i = this.datasource.getEvents();
    this.events = this.filterAndLimit(i, this.countLimit);
  }

  filterAndLimit(projects, countLimit) {
    projects.sort(function(b, a) {
      // convert date object into number to resolve issue in typescript
      return  +new Date(a.date) - +new Date(b.date);
    })
    
    let list: Project[] = Array();
    projects.forEach(element => {
      if (element.current) {
        list.push(element);
      }
    });

    let i = 0;
    for (let index = 0; index < projects.length; index++) {
      const element = projects[index];

      if (!element.current) {
        i++;
        list.push(element);
        if (i === countLimit) {
          break;
        }

      }

    }


    return list;
  }
}



