import { Component, OnInit, Input } from '@angular/core';
import { Project } from '../../model';
import { GoogleAnalyticsService } from '../../Services/google-analytics.service';

@Component({
  selector: 'app-project-item',
  templateUrl: './project-item.component.html',
  styleUrls: ['./project-item.component.css']
})
export class ProjectItemComponent implements OnInit {

  @Input() public project: Project;
  date = "";
  haveLink = false;

  constructor(private analytics: GoogleAnalyticsService) {

  }

  ngOnInit() {

    if (this.project.current) {
      this.date = "CURRENT";
    } else {
      var month = this.project.date.getUTCMonth() + 1; //months from 1-12
      var year = this.project.date.getUTCFullYear();
      this.date = this.parseDate(month, year);
    }

    if (this.project.link != null && this.project.link != "" && this.project.link.length > 0) {
      this.haveLink = true;
    }

  }
  parseDate(month, year) {
    let m = "";
    if (month == 1) {
      m = "JAN";
    } else if (month == 2) {
      m = "FEB";
    } else if (month == 3) {
      m = "MAR";
    } else if (month == 4) {
      m = "APR";
    } else if (month == 5) {
      m = "MAY";
    } else if (month == 6) {
      m = "JUN";
    } else if (month == 7) {
      m = "JUL";
    } else if (month == 8) {
      m = "AGO";
    } else if (month == 9) {
      m = "SEP";
    } else if (month == 10) {
      m = "OCT";
    } else if (month == 11) {
      m = "NOV";
    } else if (month == 12) {
      m = "DIC";
    }
    return m + " " + String(year);
  }

  reportInteraction() {
    this.analytics.emitEvent("Engagement", this.project.role, this.project.link);
  }

}



