import { AuthGuard } from './Auth/auth-guard.service';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { NotAuthorizedComponent } from './not-authorized/not-authorized.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import { RouterModule, Routes  } from '@angular/router';
import { AppComponent } from './app.component';
import { PortfolioViewComponent } from './/Views/portfolio-view/portfolio-view.component';
import { ProjectsViewComponent } from './/Views/projects-view/projects-view.component';
import { ProjectViewComponent } from './/Views/project-view/project-view.component'; 
import { ProfileComponent } from './/Components/profile/profile.component';
import { ProjectsComponent } from './/Components/projects/projects.component';
import { PromptComponent } from './/Components/prompt/prompt.component';
import { ContactComponent } from './/Components/contact/contact.component';
import { NavbarComponent } from './/Components/navbar/navbar.component';
import { EventsComponent } from './/Components/events/events.component';
import { footerComponent } from './/Components/footer/footer.component';
import { HobbiesComponent } from './/Components/hobbies/hobbies.component';
import { InterestsComponent } from './/Components/interests/interests.component';
import { StudiesComponent } from './/Components/studies/studies.component';
import { SkillsComponent } from './/Components/skills/skills.component';
import { ProjectItemComponent } from './Components/project-item/project-item.component'; 
import { DatasourceService } from './Services/datasource.service';
import { EventsViewComponent } from './Views/events-view/events-view.component';  
import { GoogleAnalyticsService } from './Services/google-analytics.service';
import { SocialBtnComponent } from './components/social-btn/social-btn.component';
 


const views: Routes = [
  {
    path: '',
    component: PortfolioViewComponent
  },
  {
    path: 'projects',
    component: ProjectsViewComponent
  },
  {
    path: 'events',
    component: EventsViewComponent
  },
  {
    path: 'project',
    component: ProjectViewComponent
  }, 
  {
    path: 'not-authorized',
    component: NotAuthorizedComponent
  },
  {
    path: '**',
    component: PageNotFoundComponent
  }

];

@NgModule({
  declarations: [
    AppComponent,
    NotAuthorizedComponent,
    PageNotFoundComponent,
    PortfolioViewComponent,
    ProjectsViewComponent,
    ProjectViewComponent, 
    ProfileComponent,
    ProjectsComponent,
    PromptComponent,
    ContactComponent,
    NavbarComponent,
    EventsComponent,
    footerComponent,
    HobbiesComponent,
    InterestsComponent,
    StudiesComponent,
    SkillsComponent,
    ProjectItemComponent,
    EventsViewComponent, 
    SocialBtnComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(views),
  ],
  providers: [
    AuthGuard,
    DatasourceService, 
    GoogleAnalyticsService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



