import { Component, OnInit } from '@angular/core';
import { Ribbon } from '../../model';
import { GoogleAnalyticsService } from '../../Services/google-analytics.service';
import { DatasourceService } from '../../Services/datasource.service';




@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  public showRibbon: boolean;
  private ribbon: Ribbon;

  constructor(private analytics: GoogleAnalyticsService, private datasource: DatasourceService) {
    this.showRibbon = false;
  }

  ngOnInit() {
    this.ribbon = this.datasource.getRibbon();

    let token = localStorage.getItem('last-ribbon-update');
    if (this.ribbon != null) {
      if (token == null) {
        this.showRibbon = true;
      } else {
        let dateToken = new Date(token);
        let diffInMs = new Date().getTime() - dateToken.getTime();
        let diffInHours: number = diffInMs / 1000 / 60 / 60;
        if (diffInHours >= 24) {
          this.showRibbon = true;
        }
      }
    }
  }

  disponseRibbon() {
    this.showRibbon = false;
    localStorage.setItem('last-ribbon-update', new Date().toISOString());

  }

  getRibbon() {
    let ri = new Ribbon();
    ri.message = "Check out my Thesis post";
    ri.link = "https://www.google.com/"
    return ri;
  }

  download() {
    this.analytics.emitEvent("Download", "Resumen");
  }

  goToRibbonLink() {
    this.analytics.emitEvent("Ribbon", "Link", this.ribbon.link);
    window.open(this.ribbon.link);
  }

}



