import { Component, OnInit, Input } from '@angular/core';
import { GoogleAnalyticsService } from '../../Services/google-analytics.service';

@Component({
  selector: 'app-social-btn',
  templateUrl: './social-btn.component.html',
  styleUrls: ['./social-btn.component.css']
})
export class SocialBtnComponent implements OnInit {

  @Input() public analyticsZone: string;
  @Input() public size: number;

  linkedin = "https://www.linkedin.com/in/miguel-angelcampos/";
  github = "https://github.com/miguel3010";
  twitter = "https://twitter.com/Miguel_Angel_30";


  constructor(private analytics: GoogleAnalyticsService) {
    this.size = 3;
    this.analyticsZone = "profile"
  }

  ngOnInit() {
  }

  goToSocial(social) {
    if (social === "linkedin") {
      this.redirect(this.linkedin, social);
    } else if (social === "twitter") {
      this.redirect(this.twitter, social);
    } else if (social === "github") {
      this.redirect(this.github, social);
    }
  }

  redirect(url, social) {

    this.analytics.emitEvent(social, 'social-media-' + this.analyticsZone);
    window.open(url, "_blank");
  }
}



